.mobile-preview {
  width: 4.05rem;
  background: #333;
  padding: 0.15rem 0;
  border-radius: 0.12rem;
}

.mobile-preview>header {
  background: #fff;
  width: 3.55rem;
  height: 0.3rem;
  line-height: 0.3rem;
  margin: 0 auto 0.1rem auto;
  padding: 0 0.1rem;
  border-radius: 0.05rem;
  font-size: 0.12rem;
  overflow: hidden;
}

.mobile-preview>main {
  background: #fff;
  width: 3.75rem;
  margin: 0 auto;
  overflow: hidden;
}
